export default {
	data() {
		return {
			type:'',
			id:'',
			reason:''
		}
	},
	onLoad(e) {
		// 0是发布成功过来的，1是违规了
		this.type = e.type
		// 违规的id
		this.id = e.id
		this.reason = e.reason
	},
	methods: {
		UploadAgain(){
			uni.navigateTo({
				url:'/pagesFour/ServiceEdit/ServiceEdit?status=100&id=' + this.id
			})
		},
		look(){
			uni.switchTab({
				url:'/pages/service/service'
			})
		},
		again(){
			uni.redirectTo({
				url:'/pagesFour/ServiceEdit/ServiceEdit'
			})
		}
	}
}
